/*Reset bootstrap*/

html,
body {
    height: 100%;
    margin: 0px;
    padding: 0px;
}

#navbar {
    margin-bottom: 0;
}

.alert {
    border-radius: 0;
}

.container {
    width: 100%;
    padding-right: 0;
    padding-left: 0;
    margin-right: auto;
    margin-left: auto;
}

.col-xs-1,
.col-sm-1,
.col-md-1,
.col-sm-1,
.col-xs-2,
.col-sm-2,
.col-md-2,
.col-sm-2,
.col-xs-3,
.col-sm-3,
.col-md-3,
.col-sm-3,
.col-xs-4,
.col-sm-4,
.col-md-4,
.col-sm-4,
.col-xs-5,
.col-sm-5,
.col-md-5,
.col-sm-5,
.col-xs-6,
.col-sm-6,
.col-md-6,
.col-sm-6,
.col-xs-7,
.col-sm-7,
.col-md-7,
.col-sm-7,
.col-xs-8,
.col-sm-8,
.col-md-8,
.col-sm-8,
.col-xs-9,
.col-sm-9,
.col-md-9,
.col-sm-9,
.col-xs-10,
.col-sm-10,
.col-md-10,
.col-sm-10,
.col-xs-11,
.col-sm-11,
.col-md-11,
.col-sm-11,
.col-xs-12,
.col-sm-12,
.col-md-12,
.col-sm-12 {
    position: relative;
    height: 100%;
    min-height: 1px;
    padding-right: 0;
    padding-left: 0;
}

.row {
    margin-right: 0;
    margin-left: 0;
}

.footer {
    margin-top: 0;
    padding-top: 0;
    padding-bottom: 0;
    border-top: none;
}

.panel {
    border: none;
}


/*Global*/

* {
    margin: 0;
    padding: 0;
}

html,
body {
    margin: 0;
    -web-font-smoothing: antialiased !important;
}


/* Page */

body {
    background-color: #b21f89;
    font-family: "Open Sans", sans-serif;
    display: flex;
    flex-direction: column;
    flex: 1 1;
}

.container {
    display: flex;
    justify-content: center;
}

.formulario {
    display: flex;
    justify-content: center;
}

.content {
    background-image: url(/static/media/bg-form.453e928c.png);
    min-width: 737px;
    height: 665px;
    display: flex;
}

.content>.container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 350px;
}

.content>.container>h1 {
    font-family: "Pacifico", cursive;
    color: #b21f89;
    font-size: 40px;
    margin-top: 15px;
    margin-bottom: 15px;
}

.content>.container>p {
    font-family: "Open Sans", sans-serif;
    font-size: 14px;
    color: #b21f89;
    text-align: center;
    margin-bottom: 15px;
}

.content>.container>form {
    width: 80%;
}

.content>.container>form>div>input[type="text"],
.content>.container>form>div>input[type="email"],
.content>.container>form>div>input[type="number"] {
    margin-bottom: 0px;
    text-align: center;
    color: #b21f89;
    background-color: transparent;
    display: block;
    border: none;
    border-bottom: #b21f89 1px solid;
    font-size: 18px;
    width: 100%;
    padding: 10px 0;
}

.content>.container>form>div>input[type="text"]:focus,
.content>.container>form>div>input[type="email"]:focus,
.content>.container>form>div>input[type="number"]:focus {
    outline: none;
}

.content>.container>form>div>input[type="text"]::-webkit-input-placeholder, .content>.container>form>div>input[type="email"]::-webkit-input-placeholder, .content>.container>form>div>input[type="number"]::-webkit-input-placeholder {
    color: #b21f89;
    font-size: 13px;
    text-align: center;
}

.content>.container>form>div>input[type="text"]::-moz-placeholder, .content>.container>form>div>input[type="email"]::-moz-placeholder, .content>.container>form>div>input[type="number"]::-moz-placeholder {
    color: #b21f89;
    font-size: 13px;
    text-align: center;
}

.content>.container>form>div>input[type="text"]:-ms-input-placeholder, .content>.container>form>div>input[type="email"]:-ms-input-placeholder, .content>.container>form>div>input[type="number"]:-ms-input-placeholder {
    color: #b21f89;
    font-size: 13px;
    text-align: center;
}

.content>.container>form>div>input[type="text"]::-ms-input-placeholder, .content>.container>form>div>input[type="email"]::-ms-input-placeholder, .content>.container>form>div>input[type="number"]::-ms-input-placeholder {
    color: #b21f89;
    font-size: 13px;
    text-align: center;
}

.content>.container>form>div>input[type="text"]::placeholder,
.content>.container>form>div>input[type="email"]::placeholder,
.content>.container>form>div>input[type="number"]::placeholder {
    color: #b21f89;
    font-size: 13px;
    text-align: center;
}

.error-input {
    display: block;
    text-align: center;
    font-size: x-small;
    color: #df0a0a;
}

.btn-container {
    width: 100%;
    text-align: center;
    margin-top: 10px;
}

.btn-cadastrar {
    background-color: transparent;
    color: #b21f89;
    font-size: 30px;
    font-family: "Pacifico", cursive;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 5px;
}

@media only screen and (max-width: 600px) {
    html,
    body {
        margin: 0px;
        padding: 0px;
    }
    body {
        display: block;
    }
    .logo {
        display: block;
        min-width: 300px;
        text-align: center;
    }
    .logo img {
        width: 80%;
        margin: 0 auto;
    }
    .content {
        background-image: url(/static/media/bg-form.453e928c.png);
        background-position-x: center;
        min-width: 300px;
        height: 665px;
        display: flex;
    }
    .formulario {
        display: block;
        margin: 0 auto;
    }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

